// ==========================================================================
// -- Configuration Settings --
// ==========================================================================
$include-shades: true; // include color shades
$base-extended: true; // include extra base features e.g. hr.mm
// $grid-has-gap: true;

$img-path: "/images" !default;

// ==========================================================================
// -- Base Styles --
// ==========================================================================

$base-color: #333 !default;
$base-bg: #fff !default;
// $base-font-family: Poppins, system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji' !important;
// $base-font-family: "Montserrat", sans-serif;
$base-font-family: scandia-web, ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important;
$base-font-weight: normal !default;
$base-font-size: 1rem !default;
$base-line-height: 1.5 !default;
$base-border-color: #ddd !default;

// ==========================================================================
// -- Typography --
// ==========================================================================
$font-size-sm: ($base-font-size * 0.875) !default;
$font-size-md: ($base-font-size * 1.25) !default;
$font-size-lg: ($base-font-size * 1.5) !default;

$heading-sizes: 2.25em, 1.75em, 1.5em, 1.25em, 1.125em, 1em;
// $heading-color: $base-color !default;
$heading-color: inherit !default;
$heading-font-weight: 700;

$link-color: #5850ec !default;
$link-hover-color: indianred !default;
// $link-color: #007bff !default;
// $link-hover-color: #0056b3 !default;

// ==========================================================================
// -- Spacing Sizes --
// ==========================================================================

// grid-gap, is the space used to separate grid elements, page sections or components
// content-gap, is the padding or margin that separates content. For example, <p>

// use em so gap relative to font size, this means h2 gap will be larger than p
$content-gap: 1.25em;
$content-gap-lg: 3em;
$content-gap-xl: 5em;
$content-gap-xxl: 8em;

$grid-gap: 2rem;
$grid-gap-lg: 3rem;
$grid-gap-xl: 5rem;

// ==========================================================================
// -- NAYKEL Template Styles --
// ==========================================================================
$header-bg: #074464 !default;

// ==========================================================================
// -- Responsive Sizes and Breakpoints --
// ==========================================================================
$breakpoints: (
    sm: 576px,
    md: 768px,
    lg: 1024px,
    xl: 1200px,
    xxl: 1600px,
);

$devices-map: (
    mobile: map-get($breakpoints, sm),
    tablet: map-get($breakpoints, md),
    laptop: map-get($breakpoints, lg),
    desktop: map-get($breakpoints, xl),
    widescreen: map-get($breakpoints, xxl),
);

// translate breakpoints into user friendly names for easy reference
// these could be referred to as 'display when' sizes, display when (min-width: $tablet)
$mobile: map-get($breakpoints, sm);
$tablet: map-get($breakpoints, md);
$laptop: map-get($breakpoints, lg);
$desktop: map-get($breakpoints, xl);
$widescreen: map-get($breakpoints, xxl);

$container-tablet: 45rem !default; // 720px
$container-laptop: 60em !default; // 960px
$container-desktop: 75em !default; // 1200px
$container-widescreen: 75em !default; // 1344px
// $container-widescreen: 84em !default; // 1344px
