// only put basics styles here, it is impossible to create a one-size fits all
.hero-text {
//     text-align: center;
//     line-height: 1;

    .title {
        color: $hero-title-color;
        font-size: 2.25rem;

        @include from-tablet {
            font-size: 3.5rem;
        }

        @include from-desktop {
            font-size: 4rem;
        }
    }

    .subtitle {
        color: $hero-subtitle-color;
        font-size: 1.5rem;

        @include from-desktop {
            font-size: 2.1rem;
        }

    }
}
