@use "../abstracts/position_sizes" as *;
@use "../mixins/media" as *;
@use "../base/vars_base" as *;

.mal {
    margin-left: auto !important;
}

.mar {
    margin-left: auto !important;
}

.mmxy {
    margin: -$content-gap;
}
.mmx {
    margin-left: -$content-gap;
    margin-right: -$content-gap;
}
// remove margin and padding (top, bottom, left, right, xy)
@each $position, $direction in map-merge($tblr, $xy) {
    @if $position == "x" {
        .nm#{$position} {
            margin-left: 0 !important;
            margin-right: 0 !important;
        }
        .np#{$position} {
            padding-left: 0 !important;
            padding-right: 0 !important;
        }
        // alignment
        .ma#{$position} {
            margin-left: auto !important;
            margin-right: auto !important;
        }
    } @else if $position == "y" {
        .nm#{$position} {
            margin-top: 0 !important;
            margin-bottom: 0 !important;
        }
        .np#{$position} {
            padding-top: 0 !important;
            padding-bottom: 0 !important;
        }
        // this seems redundant, use flexbox and va-c
        // .ma#{$position} {
        //     margin-top: auto !important;
        //     margin-bottom: auto !important;
        // }
    } @else {
        .nm#{$position} {
            margin#{$direction}: 0 !important;
        }
        .np#{$position} {
            padding#{$direction}: 0 !important;
        }
    }
}

// margin and padding top, bottom, left, right, xy
@each $position, $direction in map-merge($tblr, $xy) {
    @each $key, $size in $sizes {
        @if $position == "x" {
            .mx#{$key} {
                margin-left: $size !important;
                margin-right: $size !important;
            }
            .px#{$key} {
                padding-left: $size !important;
                padding-right: $size !important;
            }
        } @else if $position == "y" {
            .my#{$key} {
                margin-top: $size !important;
                margin-bottom: $size !important;
            }
            .py#{$key} {
                padding-top: $size !important;
                padding-bottom: $size !important;
            }
        } @else {
            .m#{$position}#{$key} {
                margin#{$direction}: $size !important;
            }
            .p#{$position}#{$key} {
                padding#{$direction}: $size !important;
            }
        }
    }
}

// Depreciated
// ----------------------------
.nm {
    margin: 0 !important;
}

.np {
    padding: 0 !important;
}

