@use '../mixins/media' as *;
@use '../base/vars_base' as *;
@use 'sass:map';

$num-cols: 6; // number of classes to make

.grid {
    display: grid;
    gap: $content-gap;
}

.grid.t{
    grid-template-columns: repeat(6, 1fr);
}

.grid-gap {
    gap: 1.25em;
}

.gg-lg {
    gap: $grid-gap-lg;
}

// css grid fixed columns
// -------------------------
@for $i from 1 through $num-cols {
    .cols-#{$i} {
        grid-template-columns: repeat($i, minmax(0, 1fr));
    }
}

// responsive grid
// -------------------------
// for each breakpoint (xs, sm, etc ...) in breakpoints
@each $breakpoint, $size in map.remove($breakpoints, "xs") {
    // create a media query with min-width of $size
    @media (min-width: $size) {
        // create num-cols + 1 for 0 index
        @for $i from 1 to $num-cols + 1 {
            .cols-#{$breakpoint}-#{$i} {
                grid-template-columns: repeat($i, minmax(0, 1fr));
            }
        }
    }
}
