// colour maps and themes
// ------------------------------------------
// only create variables that are likely to be overridden
// 
// 

$primary: hsl(201, 100%, 14%) !default;
$secondary: hsl(31, 100%, 48%) !default;

$color-map: (
    "white": #fff,
    "red": hsl(354, 70%, 54%),
    "green": hsl(134, 61%, 41%),
    "blue": hsl(204, 83%, 53%),
    "yellow": hsl(48, 100%, 67%),
    "orange": hsl(14, 100%, 50%),
    "cyan": hsl(180, 100%, 50%),
    "teal": hsl(180, 100%, 25%),
    "turquoise": hsl(171, 100%, 41%),
    "purple": hsl(271, 100%, 71%),
    "darkblue": hsl(201, 100%, 14%),
    "indigo": hsl(263, 90%, 51%),
    "pink": hsl(332, 79%, 58%),
    "primary": $primary,
    "secondary": $secondary,
    "light": hsl(0, 0%, 98%),
    "dark": hsl(0, 0%, 13%),
    "muted": hsl(0, 4%, 51%),
    "danger": hsl(354, 70%, 54%),
    "success": hsl(134, 61%, 41%),
    "info": hsl(204, 83%, 53%),
    "warning": hsl(48, 100%, 67%),
    "danger-light": hsl(347, 90%, 96%),
    "info-light": hsl(206, 70%, 96%),
    "success-light": hsl(142, 52%, 96%),
    "warning-light": hsl(48, 100%, 96%),
);

// ----------------------------------
$themes-map: (
    "primary": (
        "base": $primary,
        "color": white,
    ),
    "secondary": (
        "base": $secondary,
    ),
    "danger": (
        "base": map-get($color-map, "danger"),
        "base-light": map-get($color-map, "danger-light"),
        "color-light": map-get($color-map, "danger"),
    ),
    "success": (
        "base": map-get($color-map, "success"),
        "base-light": map-get($color-map, "success-light"),
        "color-light": map-get($color-map, "success"),
    ),
    "info": (
        "base": map-get($color-map, "info"),
        "base-light": map-get($color-map, "info-light"),
        "color-light": map-get($color-map, "info"),
    ),
    "warning": (
        "base": map-get($color-map, "warning"),
        "base-light": map-get($color-map, "warning-light"),
        "color-light": map-get($color-map, "warning"),
    ),
);
