@use '../abstracts/colors' as *;

$text-utilities: (
    "font-weight": (
        "prefix": "fw",
        "values": (
            4: 400,
            6: 600,
            7: 700,
            9: 900,
        ),
    ),
    "line-height": (
        "prefix": "lh",
        "values": (
            1: 1,
        ),
    ),
    "text-align": (
        "prefix": "ta",
        "values": (
            "l": left,
            "r": right,
            "c": center,
            "j": justify,
        ),
    ),
    "text-transform": (
        "prefix": "txt-",
        "values": (
            "strike": line-through,
            "underline": underline,
            "upper": uppercase,
            "lower": lowercase,
            "capitalize": capitalize,
        ),
    ),
);

@each $property, $map in $text-utilities {
    $prefix: map-get($map, "prefix");
    $values: map-get($map, "values");

    @each $key, $value in $values {
        .#{$prefix}#{$key} {
            #{$property}: $value;
        }
    }
}

// |--\0/-- Text Modifiers --\0/--|
.txt {
    // size modifiers
    &-sm {
        font-size: 0.875rem;
        line-height: 1.25rem;
    }
    &-lg {
        font-size: 1.125rem;
        line-height: 1.75rem;
    }
    &-xl {
        font-size: 1.25rem;
        line-height: 1.75rem;
    }

    // colour modifiers
    &-green {
        color: map-get($color-map, "green");
    }

    &-red {
        color: map-get($color-map, "red");
    }

    &-orange {
        color: map-get($color-map, "orange");
    }

    &-blue {
        color: map-get($color-map, "blue");
    }

    &-white {
        color: #fff;
    }

    &-muted {
        color: var(--muted);
    }

    &-primary {
        color: $primary;
    }

    &-secondary {
        color: var(--secondary);
    }
}
