@use '../mixins/media' as *;
@use '../base/vars_base' as *;


.container {
  // width: 100%;
  width: min(90%);
  margin-right: auto;
  margin-left: auto;
  position: relative;
}

@media (min-width: $tablet) {
  .container {
    max-width: $container-tablet;
  }
}

@media (min-width: $desktop) {
  .container {
    max-width: $container-desktop;
  }
}

@media (min-width: $widescreen) {
  .container {
    max-width: $container-widescreen;
  }
}


