@use "../abstracts/themes" as *;
@use "../abstracts/colors" as *;
@use "../forms/control" as *;
@use "../functions/setTextColor" as *;
@use "../mixins/state" as *;
@use "../base/vars_base" as *;

$btn-color: #363636 !default;
$btn-bg: rgb(248, 248, 248) !default;
$btn-link-color: $link-color;
$btn-link-hover-color: $link-hover-color;

// override base element for basic style ???
// ----------------------------
// input[type="submit"],
// input[type="reset"],
// button {
//     @extend .btn;
// }

// base class
// ----------------------------

[class*="btn"] {
    &.focus,
    &:focus {
        outline: none;
    }
}

// a[class*="btn"] {
//     text-decoration: none;
// }

.btn {
    &:hover {
        color: $btn-color;
        text-decoration: none;
        background-color: darken($btn-bg, 5%);
    }

    &:not(:disabled):not(.disabled) {
        cursor: pointer;
    }
}

// order matters, leave below .btn
button,
.btn {
    @extend %control; // core from _control.scss
    background: $btn-bg;
    color: $btn-color;
    transition-duration: 115ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform;
    user-select: none;
    justify-content: center;

    &.link {
        background-color: transparent;
        background-image: none;
        border: none;
        color: $btn-link-color;

        &:hover {
            color: $btn-link-hover-color;

            // @include active(darken($btn-color, 5%));
            // text-decoration: none;
        }
    }

    .icon{
        height: 1.25em;
    }
}

// themes and variations
// ----------------------------
//
// **! must explicitly state theme values to override base 'btn' class !**
//

@each $key, $color in $color-map {
    .btn.#{$key} {
        background: $color;
        color: setTextColor($color);

        @include hoverTransition(lighten($color, 10%));
        @include active(darken($color, 5%));

        &.outline {
            @include hoverTransition(lighten($color, 5%)) {
                color: setTextColor($color);
            }
            @include active(darken($color, 5%));
        }
    }
}
