// there are two maps for 'flex-justify' this is by design. I prefer to use shorthand for
// simple left, right and center alignment but the full property name for everything else
// $utilities: (

$flex-utilities: (
    "flex-justify-content": (
        "property": "justify-content",
        "prefix": "ha-",
        "values": (
            l: flex-start,
            r: flex-end,
            c: center,
        ),
    ),
    "flex-justify-content-full": (
        "property": "justify-content",
        "prefix": "",
        "values": (
            space-between: space-between,
            space-around: space-around,
            space-even: space-evenly,
        ),
    ),
    "flex-align-items": (
        "property": "align-items",
        "prefix": "va-",
        "values": (
            t: flex-start,
            b: flex-end,
            c: center,
            start: start,
            stretch: stretch,
            baseline: baseline,
        ),
    ),

    "flex-direction": (
        "property": "flex-direction",
        "prefix": "",
        "values": (
            column: column,
            column-reverse: column-reverse,
            row: row,
            row-reverse: row-reverse,
        ),
    ),
);

@each $property, $map in $flex-utilities {
    $property: map-get($map, "property");
    $prefix: map-get($map, "prefix");
    $values: map-get($map, "values");

    @each $key, $v in $values {
        .#{$prefix}#{$key} {
            #{$property}: $v;
        }
    }
}

.flex {
    display: flex;
    flex-wrap: wrap;

    &.nowrap {
        flex-wrap: nowrap;
    }
  
}

.inline-flex {
    display: inline-flex;
}

.flex-col {
    display: flex;
    flex-direction: column;
}


.flex .no-wrap {
    flex-wrap: nowrap;
}
.flex .wrap {
    flex-wrap: wrap;
}


$flex-grow-map: (
    "0": 0,
    "1": 1,
    "2": 2,
);

// covers both grow and shrink
@each $key, $value in $flex-grow-map {
    .fg#{$key} {
        flex-grow: $value;
    }
    .fs#{$key} {
        flex-shrink: $value;
    }
}