@use '../mixins/media' as *;

$visibility-utilities: (
    "display": (
        "property": "display",
        "prefix": "",
        "values": (
            dblk: block,
            dil: inline,
            dilb: inline-block,
            dflx: flex,
            dilf: inline-flex,
            hidden: none,
        ),
    ),
);

@each $property, $map in $visibility-utilities {
    $property: map-get($map, "property");
    $prefix: map-get($map, "prefix");
    $values: map-get($map, "values");

    @each $key, $v in $values {
        .#{$prefix}#{$key} {
            #{$property}: $v;
        }
    }
}


// ==========================================================================
// |--\0/-- device-only --\0/--|
// --------------------------------------------------------------------------
@include mobile-only {
    .hide-on-mobile {
        display: none !important;
    }
    .block-on-mobile {
        display: block !important;
    }
    .flex-on-mobile {
        display: flex !important;
    }
}
@include tablet-only {
    .hide-on-tablet {
        display: none !important;
    }
    .block-on-tablet {
        display: block !important;
    }
    .flex-on-tablet {
        display: flex !important;
    }
}
@include desktop-only {
    .hide-on-desktop {
        display: none !important;
    }
    .block-on-desktop {
        display: block !important;
    }
    .flex-on-desktop {
        display: flex !important;
    }
}
