@use '../base/vars_base' as *;

$nav-padding-y: 0.5rem !default;
$nav-padding-x: 0.75rem !default;

$nav-color: #eee !default;
$nav-bg: white !default;
$nav-hover-bg: #efefef !default;

nav {
    display: flex;

    a {
        text-decoration: none;
    }
    // :not(.btn) is to let btn classes look after styling if used
    a:not([class*="btn"]) {
        padding: $nav-padding-y $nav-padding-x;
    }
}

// ====================================================================== -->
// Base nav class - basis for all `<nav>`s or `<ul>`s navigation's
// ====================================================================== -->
.nav {
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    flex-direction: column;

    // nav items
    a {
        padding: $nav-padding-y $nav-padding-x;
        display: block;
        color: setTextColor($nav-bg);

        &:hover {
            color: setTextColor($nav-hover-bg);
            background: $nav-hover-bg;
        }
    }

    .heading {
        margin-top: 1rem;
        font-weight: 600;
        color: #7a7a7a;
    }
}
