.fullwidth {
    width: 100%;
    max-width: 100%;
}

.round {
    border-radius: 50%;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(31, 41, 55, 0.6);
    display: flex;
    justify-content: center;
    align-items: start;
    z-index: 1000;
}

.cursor-pointer {
    cursor: pointer;
}

.no-click {
    pointer-events: none;
}

.close {
    color: rgb(220, 20, 60);
    cursor: pointer;
    &:hover {
        color: rgb(212, 64, 93);
        cursor: pointer;
    }
    &:active {
        color: rgb(220, 20, 60);
        cursor: pointer;
    }
}
