@use "../mixins/media" as *;
@use "../base/vars_base" as *;

// Content Spacing
// ===================

// 1. class is '.frm-row', not a first-child
//    and the previous element is not a hidden input

// 2. :not(bx-) will exclude header, content, and footer ?????
// :is(.flex, .grid, .bx):not(:first-child), // * + (element) (add top margin unless is first child)

// * > :is(.bx:not(:first-child):not(.bx-content)),
// :is(.grid, .flex, .bx) > *:not(* + .bx)

:is(.frm-row:not(* > :first-child):not(input[type="hidden"] + .frm-row)), // 1
:is(p, h1, h2, h3, h4, h5, h6, pre, section, form, table, ul, .bx, .grid, .flex, .frm-row) + *:not(hr):not(.overlay):not(.bx-content),
:is(.menu) + *,
* + :is(p):not(:first-child) {
    margin-top: $content-gap;
}

// IMPORTANT, do not add for flex-col use grid gap instead ??
:is(.grid, .flex) > .bx, // 1.
* > :is(p):first-child * > :is([class*="btn"]) {
    margin-top: 0;
}

// responsive spacing
.py-res {
    padding-top: $content-gap !important;
    padding-bottom: $content-gap !important;

    @include from-desktop {
        padding-top: $content-gap-lg !important;
        padding-bottom: $content-gap-lg !important;
    }
}

// py-res classes are used to add larger spacing between sections
.py-res-lg {
    padding-top: $content-gap !important;
    padding-bottom: $content-gap !important;

    @include from-laptop {
        padding-top: $content-gap-lg !important;
        padding-bottom: $content-gap-lg !important;
    }

    @include from-desktop {
        padding-top: $content-gap-xl !important;
        padding-bottom: $content-gap-xl !important;
    }
}

.py-res-xl {
    padding-top: $content-gap-lg !important;
    padding-bottom: $content-gap-lg !important;

    @include from-laptop {
        padding-top: $content-gap-xl !important;
        padding-bottom: $content-gap-xl !important;
    }

    @include from-desktop {
        padding-top: $content-gap-xxl !important;
        padding-bottom: $content-gap-xxl !important;
    }
}

// responsive content gap
%gap {
    padding-top: $content-gap;
    padding-bottom: $content-gap;

    @include from-tablet {
        padding-top: $content-gap-lg;
        padding-bottom: $content-gap-lg;
    }
}

// responsive content gap
.pxy-res {
    padding: $content-gap-lg $content-gap;
}
