@use "./mixins/media" as *;
@use "./base/vars_base" as *;
@use "./abstracts/content_gap" as *;

$main-bg: rgb(243, 244, 246) !default;

$footer-bg: rgb(25, 28, 29) !default;
$footer-text-color: rgb(204, 204, 204) !default;

$copyright-bg: darken($footer-bg, 2%) !default;
$copyright-text-color: $footer-text-color !default;

#nk-header {
    background: $header-bg;
    padding: $content-gap;
}

#nk-main {
    flex: 1; // 1. allow to grow to fill height
    // background: $main-bg;
    margin: 0;
}

footer {
    background: $footer-bg;
    margin-top: auto; // forces footer to the bottom

    nav {
        text-align: center;

        a {
            padding: 0 1rem;
            color: #ccc;
            font-size: 0.8rem;

            &:hover {
                color: #ddd;
                background: #444;
            }
        }
    }
}

.copyright {
    color: $copyright-text-color;
    background: $copyright-bg;
    padding: 0.5rem;
    text-align: center;
    font-size: 0.9rem;
    border: 0; // remove border from colour scheme
    width: 100%;
}

#logo {
    text-align: center;
    height: 40px;

    @include from-tablet {
        height: 60px;
        text-align: left;
    }

    @include from-desktop {
        height: auto;
    }
}

// NK Admin Styles
// --------------------------------------------------------------------------
.nk-admin {
    .navbar {
        background-image: linear-gradient(to right top, #20242a, #1e1f27, #1e1a23, #1f141d, #1f0e16, #250e15, #2c0e12, #310f0c, #40140e, #4e1a0e, #5c210c, #692906);
    }
}

// NK Documentation Styles
// --------------------------------------------------------------------------

.icon-box {
    border: 1px solid #ccc;
    padding: 0.5rem 0;

    svg {
        height: 72px !important;
    }

    .icon {
        width: 150px; // icon container width
    }

    .icon-title {
        display: block;
        text-align: center;
        font-size: 0.875rem;
        margin-top: -0.5rem;
    }
}

